//  resource backend -- https://connections-api.herokuapp.com/docs/#/
import { createAsyncThunk } from '@reduxjs/toolkit';
import axios from 'axios';

axios.defaults.baseURL = 'https://connections-api.herokuapp.com/'; //     URL

//    ( )     backend.
const setAuthHeader = token => {
  axios.defaults.headers.common.Authorization = `Bearer ${token}`;
};

//   de  ( ).
const clearAuthHeader = () => {
  axios.defaults.headers.common.Authorization = '';
};

// credentials (  ) --       input
export const registerThunk = createAsyncThunk(
  'auth/register',
  async (credentials, thunkAPI) => {
    try {
      const { data } = await axios.post('/users/signup', credentials);
      setAuthHeader(data.token); //    http-

      return data;
    } catch (error) {
      return thunkAPI.rejectWithValue(error.message);
    }
  }
);

export const loginThunk = createAsyncThunk(
  'auth/login',
  async (credentials, thunkAPI) => {
    try {
      const { data } = await axios.post('/users/login', credentials);
      setAuthHeader(data.token); //    http-

      return data;
    } catch (error) {
      return thunkAPI.rejectWithValue(error.message);
    }
  }
);

export const logoutThunk = createAsyncThunk(
  'auth/logout',
  async (_, thunkAPI) => {
    try {
      await axios.post('/users/logout');
      clearAuthHeader(); //    
    } catch (error) {
      return thunkAPI.rejectWithValue(error.message);
    }
  }
);

//     
export const refreshUserThunk = createAsyncThunk(
  'auth/refresh',
  async (_, thunkAPI) => {
    //     redux state (..   user  )
    const state = thunkAPI.getState(); //   state  Redux (thunkAPI   )
    const token = state.auth.token; //   --> /const { token } = thunkAPI.getState().auth;/

    if (!token) {
      return thunkAPI.rejectWithValue('No valid token');
    } //  user  , 

    try {
      setAuthHeader(token); //  user ,    http-
      const { data } = await axios.get('/users/current'); // http-  refreshUser

      return data;
    } catch (error) {
      return thunkAPI.rejectWithValue(error.message);
    }
  }
);
